<template>
  <div>
  <VueTooltip :isShow.sync="isShow" target="click"
              placement="bottom-start"
              :modifiers="{
                computeStyle: {
                  gpuAcceleration: false
                }
              }"
              :class="[activeClass ? 'active-popper': '']"
              content="bottom-start,  yes or no">
    <em class="popper-tit"> {{settlementFundList.length}}  ></em>
    <template slot="content">
      <div class="payacounts-parent">
          <div class="popper-header">
            <span class="time"></span>
            <div class="info"></div>
          </div>
          <div class="popper-content"  v-for="item in settlementFundList">
            <section class="item">
              <span class="time">{{item.transferTime}}</span>
              <div class="info">
                <p>{{item.amount}}</p>
                <p>{{item.payerBankName}}</p>
                <p>{{item.payerCardNo}}</p>
                <p>{{item.bankFundId}}</p>
              </div>
            </section>
          </div>
      </div>
    </template>
  </VueTooltip>
  </div>
</template>
<style>
</style>
<script>
  export default{
    props: ['settlementFundList'],
    data () {
      return {
        msg: 'hello vue',
        isShow: false,
        activeClass: false
      }
    },
    components: {
    },
    watch: {
      isShow (val) {
        if (val) {
          this.activeClass = true
        } else {
          this.activeClass = false
        }
      }
    }
  }
</script>



